.fugiat-quo{
    background:#FFFFFF;
    position: relative;
    @media screen and (max-width:576px) {
        display: none;
    }
    &::after{
        content:'';
        position: absolute;
        background: linear-gradient(224.72deg, #3023AE -1%, #C86DD7 100.5%);
        display: block;
        bottom: -22%;
        left: 37%;
        width: 100px;
        height: 100px;
        border-radius: 50%        ;
        -webkit-border-radius: 50%        ;
        -moz-border-radius: 50%        ;
        -ms-border-radius: 50%        ;
        -o-border-radius: 50%        ;
        z-index: 6;
        @media screen and (max-width:992px) {
            grid-template-columns: repeat(2, 1fr);
        }
        @media screen and (max-width:768px) {
            left: 58%;
        }
        @media screen and (max-width:576px) {
            left: 88%;
        }
}
    &__wrapper{
        margin: 71px 0 80px;
        display: flex;
        flex-direction: column;
    }
    &__top{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__title{
        margin-bottom: 23px;
    }
    &__text{
        margin-bottom: 32px;
        max-width: 570px;
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        color: #333333;
        text-align: center;
    }
    &__cards{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        row-gap: 54px;
        column-gap: 54px;
        @media screen and (max-width:992px) {
            grid-template-columns: repeat(2, 1fr);
        }
        @media screen and (max-width:768px) {
            row-gap: 15px;
            column-gap: 15px;
        }
        @media screen and (max-width:360px) {
            grid-template-columns: repeat(1, 1fr);
        }
    }
    &__card{
        max-width: 322px;
        padding: 40px 24px 32px;
        display: flex;
        flex-direction: column;
        align-items: center;
        background: #FFFFFF;
        box-shadow: 16px 16px 48px rgba(0, 0, 0, 0.0994678);
        border-radius: 3px;
        &:hover{
            transform: scale(1.2);
            -webkit-transform: scale(1.2);
            -moz-transform: scale(1.2);
            -ms-transform: scale(1.2);
            -o-transform: scale(1.2);
            transition: 0.5s;
            -webkit-transition: 0.5s;
            -moz-transition: 0.5s;
            -ms-transition: 0.5s;
            -o-transition: 0.5s;
            @media screen and (max-width:360px) {
                transform:none;            ;
                -webkit-transform:none            ;
                -moz-transform:none            ;
                -ms-transform:none            ;
                -o-transform:none            ;
            }
        }
        @media screen and (max-width:360px) {
            max-width: 320px;
        }
    }
}

.card{
    &__icon{}
    &__title{
        font-family: Poppins, sans-serif;
        font-style: normal;
        font-weight: 700;
        font-size: 24px;
        line-height: 32px;
        text-align: center;
        color: #333333;
        margin: 13px 0 9px;
    }
    &__text{
        text-align: center;
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
        text-align: center;
        color: #333333;
        mix-blend-mode: normal;
        opacity: 0.5;
    }
}